html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
}

img {
  display: block;
  max-width: 100%;
  width: auto;
  height: auto;
}

[class$=__container] {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

h1 {
  margin: 0;
  line-height: 1.3;
}

h2 {
  margin: 0;
  line-height: 1.3;
}

h3 {
  margin: 0;
  line-height: 1.3;
}

h1,
h2 {
  font-weight: 400;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

[class$=__heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #111;
}

p {
  font-size: 2rem;
  line-height: 1.5;
}

.header {
  padding: 5rem 0;
}
.header__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .header__grid {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 20rem;
}

@media (min-width: 768px) {
  .hero-blocks__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0;
    grid-auto-flow: dense;
  }
}

.block {
  height: 40rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.block:nth-child(1) {
  background-color: #f46669;
}
.block:nth-child(2) {
  background-image: url("../assets/img/backgrounds/bloque_1.jpg");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}
.block:nth-child(3) {
  background-color: #c7c57d;
}
@media (min-width: 768px) {
  .block:nth-child(3) {
    grid-column: 2/3;
  }
}
.block:nth-child(4) {
  background-image: url("../assets/img/backgrounds/bloque_2.jpg");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}
.block__content {
  padding: 1rem 0;
  max-width: 40rem;
  border-block: 0.1rem solid #ffffff;
  color: #ffffff;
}
.block__heading {
  text-transform: uppercase;
}

.about {
  padding: 5rem 0;
}
.about__heading {
  text-align: center;
}
.about__heading::after {
  content: "";
  display: block;
  margin: 0 auto;
  height: 8rem;
  background-image: url(../assets/img/backgrounds/separador.png);
  background-repeat: no-repeat;
  background-position: center center;
}
@media (min-width: 768px) {
  .about__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.about__block {
  text-align: center;
}
.about__img {
  margin: 0 auto;
}

.courses {
  padding: 5rem 0;
  background-color: #f5f5f5;
}
.courses__heading {
  text-align: center;
}
.courses__heading::after {
  content: "";
  display: block;
  margin: 0 auto;
  height: 8rem;
  background-image: url(../assets/img/backgrounds/separador.png);
  background-repeat: no-repeat;
  background-position: center center;
}
.courses__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
  align-items: start;
}
@media (min-width: 768px) {
  .courses__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .courses__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.hero-school {
  padding: 15rem 0;
  background-image: url("../assets/img/backgrounds/chef_bg.jpg");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  text-align: center;
  color: #ffffff;
}
.hero-school__heading {
  font-weight: 700;
}
.hero-school__text {
  margin: 0 auto;
  font-size: 2.6rem;
  max-width: 50rem;
}
.hero-school__btn {
  margin-top: 2rem;
  display: block;
  padding: 1.5rem 4rem;
  background-color: #f46669;
  font-size: 2rem;
  font-weight: 700;
  color: #ffffff;
  text-align: center;
  text-decoration: none;
  border-radius: 1rem;
  display: inline-block;
}
@media (min-width: 768px) {
  .hero-school__btn {
    width: auto;
  }
}
@media (hover: hover) {
  .hero-school__btn {
    transition: background-color 0.3s ease-in-out;
  }
  .hero-school__btn:hover {
    background-color: #f1363a;
    cursor: pointer;
  }
}

.footer {
  padding: 3rem 0;
  border-bottom: 0.5rem solid #f46669;
}
@media (min-width: 768px) {
  .footer__grid {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.footer__copy {
  text-align: center;
  font-size: 2rem;
  line-height: normal;
}
@media (min-width: 768px) {
  .footer__copy {
    margin: 0;
  }
}

.nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .nav {
    flex-direction: row;
  }
}
.nav__link {
  font-size: 2rem;
}
@media (hover: hover) {
  .nav__link {
    transition: color 0.3s ease-in-out;
  }
  .nav__link:hover {
    color: #f46669;
  }
}

.course-card {
  border: 0.1rem solid gainsboro;
  border-radius: 1rem;
  background-color: #ffffff;
  overflow: hidden;
}
.course-card__meta {
  padding: 0 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f46669;
  color: #ffffff;
}
.course-card__price {
  padding: 0.5rem;
  border-radius: 1rem;
  background-color: #c7c57d;
  color: #111;
  font-weight: 700;
}
.course-card__content {
  padding: 2rem;
}
.course-card__description {
  color: #f46669;
}
.course-card__text {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  overflow: hidden;
}
.course-card__btn {
  margin-top: 2rem;
  display: block;
  padding: 1.5rem 4rem;
  background-color: #f46669;
  font-size: 2rem;
  font-weight: 700;
  color: #ffffff;
  text-align: center;
  text-decoration: none;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .course-card__btn {
    width: auto;
  }
}
@media (hover: hover) {
  .course-card__btn {
    transition: background-color 0.3s ease-in-out;
  }
  .course-card__btn:hover {
    background-color: #f1363a;
    cursor: pointer;
  }
}